<template>
    <div class="nav animate__animated" :class="this.navBarFixed? 'navBarWrap animate__slideInDown' :'navBarAbsolute'">
      <div class="logo">
          <a href="/"><p>Pets</p></a>
      </div>
      <!-- <a>宠物图片</a> -->
      <div class="shell">
        
        <input type="text" placeholder="Search">
        <a>
            <!-- <i class="fa fa-hand-o-right"></i> -->
            <i class="fa fa-search"></i>    
        </a>
      </div>
      
      <div class="user" @mouseover="userinfoEnter" @mouseleave="userinfoLeave">
          <div class="userimg"><img src="img/user.png" ></div>
          <div class="userinfo" :class="userinfoShow ? 'disBlock' : 'disNone'">
              <div class="info">
                  <div><span class="username">{{ this.$store.state.login.userDate.username }}</span></div>
                  <div class="infoline" @click="gotoUserDetail()">
                      <!-- <router-link class="usercenter" :to="{name:'userdetail'}"></router-link> -->
                      <a class="usercenter">个人信息</a>
                      <span>></span></div>
                  <div class="infoline"><a class="history" href="">历史记录</a><span>></span></div>
                  <div class="infoline"><a class="favorite" href="">我的收藏</a><span>></span></div>
                  <div class="splitline"></div>
                  <div class="infoline" @click="userExit"><a class="loginout">退出登录</a><span>></span>
                  </div>
              </div>
          </div>
      </div>
    </div>
  </template>
  
  <script>
  
  import 'animate.css'
  import $ from 'jquery'
  
  export default {
      name : 'TopBar',
      
      data() {
          return {
              navBarFixed : false,
              userinfoShow : false,
              scrollTop : '0',
              tabList: [
                  // { id: 0, title: "首页", active : true },
                  // { id: 1, title: "关于我们", active : false },
                  // { id: 2, title: "萌宠百科", active : false },
                  // { id: 3, title: "精美图集", active : false },
                  // { id: 4, title: "联系我们", active : false },
              ]
          }
      },
      components: {},
      beforeMount() {
          this.tabList = this.$store.state.topbar.tabList
      },
      
      mounted() {
          window.addEventListener("mousewheel", this.watchScroll);
          this.watchScroll(1)
       },
      beforeDestroy() {
             window.removeEventListener("mousewheel", this.watchScroll);
        },
   
      methods: {
        // 滚动节流
		throttle(fn, delay) {
			let timer = null;
			return () => {
				if(timer) {
					return;
				}
				timer = setTimeout(() => {
					fn(1)
					timer = null;
				}, delay)
			}	
		},
        watchScroll(flash) {
            if (this.$store.state.topbar.isTopBarFlex == false) {
                return
            }
            var scrollTop =
            document.body.scrollTop ||
            document.documentElement.scrollTop ||
            window.pageYOffset;
            var sectionIndex = Math.floor((scrollTop + 400) / $(window).height())

            // 导航栏吸顶
            //  当滚动超过 90 时，实现吸顶效果
            // flash == 1 解决浏览器后退获取scrollTop为0的问题
            if (scrollTop > 90 || flash == 1) {
                this.navBarFixed = true;
            } else {
                this.navBarFixed = false;
            }

        },
        userinfoEnter() {
            this.userinfoShow = true
        },
        userinfoLeave() {
            this.userinfoShow = false
        },
        showLogin() {
            if (this.$store.state.login.isLogin) {
                return
            }
            this.$store.state.login.loginPageShow = true
        },
        userExit() {
            this.$store.state.login.isLogin = false
            Object.keys(this.$store.state.login.userDate).forEach(key => (this.$store.state.login.userDate[key] = ''));	
            this.$store.state.login.userDate.username = '游客'
        },
        gotoUserDetail() {
            var uid = this.$store.state.login.userDate.uid
            if (uid == '') {
                this.showLogin()
                return
            }else {
                this.$router.push({name:'UserDetail', query: {uid: uid}})
            }
        }
      },
      computed : {
      }
  }
  
  
  
  </script>
<style src="font-awesome/css/font-awesome.min.css"  scoped></style>
<style scoped>

.shell {
    position: relative;
    width: 50%;
    padding: 10px;
    background-color: #f7f7f7;
    border-radius: 8px;
}

.shell input {
    width: 80%;
    height: 70%;
    color: #7f7f7f;
    font: 16px '优设标题黑';
    border: 0;
    background-color: #f7f7f7;
    overflow: visible;
    outline-style: none ;
}

.shell input::placeholder {
    color: #7f7f7f;
}

.shell a {
    display: flex;
    font-size: 30px;
    position: absolute;
    right: 0;
    top: 5px;
    color: #7f7f7f;
    width: 50px;
    height: 50px;
}

  .disNone {
      display: none;
  }
  
  .disBlock {
      display: block;
  }
  
  .navBarWrap {
      position:fixed;
      top:0;
      z-index:996;
  }
  .navBarAbsolute {
      position:absolute;
      top:0;
      z-index:996;
  }
  
  .nav{
      left: 0;
      top: 0;
      height: 70px;
      width: 100%;
      padding: 0 12vw;
      margin: 0 auto;
      display: flex;
      justify-content: space-between;
      align-items: center;
      background: rgba(255,255,255,1);
      box-shadow: 0 2px 3px rgba(0, 0, 0, 0.2);
      z-index: 99;
      letter-spacing: 2px;
  }
  
  
  .logo{
      user-select: none;
      display: flex;
      justify-content: center;
      align-items: center;
  }
  .logo p{
      font-size: 40px;
      height: 40px;
      line-height: 40px;
      color: #EDC79B;
      font-weight: 800;
      font-family: "微软雅黑";
      letter-spacing: 0;
  }
  
  .nav .user{
      width: 50px;
      height: 100%;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      position: relative;
      user-select: none;
      margin-right: 90px;
  }
  .nav .user .userimg{
      display: inline-block;
      width: 50px;
      height: 50px;
      background-color: #ffffff;
      border: 3px solid #CBAA85;
      border-radius: 50%;
      overflow: hidden;
      position: absolute;
      left: 0;
  }
  
  .nav .user img{
      width: 100%;
      overflow: hidden;
  }
  .userinfo{
      height: 220px;
      width: 220px;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      border: 1px solid #E3E5E7;
      box-shadow: 0 0 30px rgb(0 0 0 / 10%);
      background-color: #ffffff;
      border-radius: 8px;
      position: absolute;
      top: 70px;
      right: -80px;
      z-index: 998;
      /* display: none; */
  }
  .userinfo .info {
      padding: 12px;
      padding-top: 10px;
      color: #61666D;
  }
  .userinfo .info .username {
      
      height: 30px;
      border-radius: 8px;
      font-size: 14px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: #61666D;
      font-size: 24px;
      font-weight: 600;
      margin-bottom: 10px;
  }
  .userinfo .info .splitline {
      height: 1px;
      width: 100%;
      background-color: #e3e5e7;
      margin-top: 9px;
      margin-bottom: 9px;
  }
  
  .userinfo .info .infoline {
      height: 35px;
      border-radius: 8px;
      font-size: 12px;
      display: flex;
      justify-content: center;
      align-items: center;
      align-content: space-between;
      color: #61666D;
      
  }
  .userinfo .info .infoline:hover {
      
      font-weight: 600;
      color: #61666D;
      background-color: #E3E5E7;
  }
  .userinfo .info .usercenter,
  .userinfo .info .history,
  .userinfo .info .favorite,
  .userinfo .info .loginout{
      width: 140px;
      font-size: 14px;
      font-weight: 600;
      color: #61666D;
  }
  
  
  </style>