<template>
    <div>
        <!-- <ResignLogin></ResignLogin> -->
        <!-- <PictureTopBar></PictureTopBar> -->
        <TopBar></TopBar>
        <div class="waterfall-contain">
            
            <vue-waterfall-easy 
            :imgsArr="imgsArr"
            :imgWidth="340"
            :maxCols="4" 
            @click="clickFn"
            @scrollReachBottom="getData">
                <div class="img-info" slot-scope="props">
                    <span :class="props.value.isfaver?'isDisplayFlex':''" 
                    @click="faverClick(props.index, props.value.id)" class="favor-icon">
                        <i :class="props.value.isfaver?'el-icon-star-on isfaver':'el-icon-star-off'"></i>
                    </span>
                    
                    <div class="text-info">
                        <p class="some-info">第{{props.index+1}}张图片</p>
                        <p class="some-info">{{props.value.info}}</p>
                    </div>

                    <div class="img-overlay">
                    
                    </div>
                </div>
            </vue-waterfall-easy>
            <div class="gettop" @click="gotoTop">
                <i class="el-icon-caret-top"></i>
            </div>
        </div>
    </div>
    
    
</template>

<script>
import vueWaterfallEasy from 'vue-waterfall-easy'
import axios from 'axios'
import $ from 'jquery'

import ResignLogin from '@/components/index/ResignLogin.vue'
import PictureTopBar from '@/components/waterfall/PictureTopBar.vue'
import TopBar from '@/components/index/TopBar.vue'

export default{
    name: 'MainPage',
    data() {
        return {
            imgsArr: [],
            group: 0, // request param
            isImgTagDispaly: false,

            temp : [
                {"id":"1001","href": "https://www.baidu.com","info": "一些图片描述文字","src": "img/animal/1672834810271.jpg","isfaver":true},
                {"id":"1001","href": "https://www.baidu.com","info": "一些图片描述文字","src": "img/animal/1672836003988.jpg","isfaver":false},
                {"id":"1001","href": "https://www.baidu.com","info": "一些图片描述文字","src": "img/animal/1672834810271.jpg","isfaver":true},
                {"id":"1001","href": "https://www.baidu.com","info": "一些图片描述文字","src": "img/animal/1672836066911.jpg","isfaver":false},
                {"id":"1001","href": "https://www.baidu.com","info": "一些图片描述文字","src": "img/animal/1672834891518.jpg","isfaver":false},
                {"id":"1001","href": "https://www.baidu.com","info": "一些图片描述文字","src": "img/animal/1672836274924.jpg","isfaver":true},
                {"id":"1001","href": "https://www.baidu.com","info": "一些图片描述文字","src": "img/animal/1672834810271.jpg","isfaver":true},
                {"id":"1001","href": "https://www.baidu.com","info": "一些图片描述文字","src": "img/animal/1672836274924.jpg","isfaver":true},
                {"id":"1001","href": "https://www.baidu.com","info": "一些图片描述文字","src": "img/animal/1672834810271.jpg","isfaver":true},
                {"id":"1001","href": "https://www.baidu.com","info": "一些图片描述文字","src": "img/animal/1672834810271.jpg","isfaver":true},
                {"id":"1001","href": "https://www.baidu.com","info": "一些图片描述文字","src": "img/animal/1672836298604.jpg","isfaver":true},
                {"id":"1001","href": "https://www.baidu.com","info": "一些图片描述文字","src": "img/animal/1672834790988.jpg","isfaver":false},
                {"id":"1001","href": "https://www.baidu.com","info": "一些图片描述文字","src": "img/animal/1672836110864.jpg","isfaver":true},
                {"id":"1001","href": "https://www.baidu.com","info": "一些图片描述文字","src": "img/animal/1672834810271.jpg","isfaver":true},
                {"id":"1001","href": "https://www.baidu.com","info": "一些图片描述文字","src": "img/animal/1672836110864.jpg","isfaver":false},
                {"id":"1001","href": "https://www.baidu.com","info": "一些图片描述文字","src": "img/animal/1672834810271.jpg","isfaver":true},
                {"id":"1001","href": "https://www.baidu.com","info": "一些图片描述文字","src": "img/animal/1672836110864.jpg","isfaver":false},
