                {"id":"1001","href": "https://www.baidu.com","info": "一些图片描述文字","src": "img/animal/1672835506927.jpg","isfaver":true},          ]
        }
    },
    components: {
        vueWaterfallEasy,
        ResignLogin,
        PictureTopBar,
        TopBar
        
    },
    created() {
        this.$store.state.topbar.isTopBarFlex = true
        this.$store.state.topbar.type = 'Search'
        this.getData()
    },
    beforeMount() {
        this.$store.state.topbar.tabList.forEach((item, index) => {
            if (index == 0) {
                this.$store.state.topbar.tabList[index].active = true
            }else {
                this.$store.state.topbar.tabList[index].active = false
            }
        })
        
    },
    mounted() {
        window.addEventListener("mousewheel", this.handleScroll);
        this.handleScroll()
    },
    beforeDestroy() {
        window.removeEventListener("mousewheel", this.handleScroll);
    }
    ,methods: {
        getData() {
            axios.get('./static/mock/data.json?group=' + this.group) // 真实环境中，后端会根据参数group返回新的图片数组，这里我用一个惊呆json文件模拟
            .then(res => {
                this.imgsArr = this.imgsArr.concat(res.data)
                this.group++
            })
            this.imgsArr = this.imgsArr.concat(this.temp)
            this.group++
        },
        handleScroll() {
            let scrollTop = document.documentElement.scrollTop || document.body.scrollTop // 滚动条距离顶部的距离
            let windowHeight = document.documentElement.clientHeight || document.body.clientHeight // 可视区的高度
            let scrollHeight = document.documentElement.scrollHeight || document.body.scrollHeight //dom元素的高度，包含溢出不可见的内容
            let that = this;
            // console.log('scrollTop' + scrollTop);
            // console.log('windowHeight' + windowHeight);
            // console.log('scrollHeight' + scrollHeight);

            // 滚动条到底部的条件scrollTop + windowHeight === scrollHeight
            if (scrollTop + windowHeight >= scrollHeight - 550) {
                console.log("滚动触底");
                this.getData();

            }
            
        },
        clickFn(event, { index, value }) {
            // 阻止a标签跳转
            event.preventDefault()
            // 只有当点击到图片时才进行操作
            if (event.target.tagName.toLowerCase() == 'img') {
            console.log('img clicked',index, value)
            }
        },
        faverClick(index, id) {

            var isFaver = this.imgsArr[index].isfaver;
            console.log("index " + index + " id " + id + " " + isFaver);

            if (isFaver) {
                // 移除收藏
                axios.post('removeFaverPicture', {
                    imgId:id
                }).then(res => {
                    console.log(this.imgsArr[index] + " 移除收藏")
                })
                this.imgsArr[index].isfaver = false;

            }else{
                // 添加收藏
                axios.post('addFaverPicture', {
                    imgId:id
                }).then(res => {
                    console.log(this.imgsArr[index] + " 添加收藏")
                })
                this.imgsArr[index].isfaver = true;

            }
            console.log(this.imgsArr[index])


        },
        gotoTop() {
            $('html,body').animate({scrollTop:"0"});
        },
        // imgMouseEnter(index) {
        //     console.log(this.imgsArr[index] + "imgMouseEnter")

        //     this.imgsArr[index].isImgTagDispaly = true;
        // },
        // imgMouseLeave(index) {
        //     console.log(this.imgsArr[index] + "imgMouseLeave")

        //     this.imgsArr[index].isImgTagDispaly = false;
        // },


    }
    
}
